.search-form {
  justify-content: center;
  margin-bottom: 24px;
  padding: 8px;
  display: flex;
}

.form-control {
  margin-right: 4px;
  padding-top: 2px;
  padding-bottom: 2px;
}

.button {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.button.is-hidden, .button > .spinner.is-hidden {
  display: none;
}

.btn-primary {
  color: #fff;
  background-color: #2659c7;
  border-color: #2659c7;
  border-radius: 4px;
  padding: 2px 24px;
  font-weight: 400;
}

.articles {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 0 auto 30px;
  padding: 0;
  list-style: none;
  display: grid;
}

.articles li {
  height: 450px;
  overflow: hidden;
}

.articles img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

.articles a {
  color: #2a2a2a;
  text-decoration: none;
}

.spinner-border-sm {
  width: 1rem;
  height: 1rem;
  border-width: .2em;
}

.spinner-border {
  vertical-align: text-bottom;
  border: .25rem solid;
  border-right-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: spinner-border .75s linear infinite;
  display: inline-block;
}

li {
  height: 650px;
}

